.header {
    display: inline-block;
    width: 100%;
    font-family: "Poppins";
    font-size: 4rem;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    margin: 0 auto;
    margin-top: 2rem;
    margin-bottom: 1.5rem;
    color: var(--black-1);

    @media only screen and (max-width: 768px) {
        font-size: 3rem;
    }
}
