.contentWrapper {
  background-color: var(--bg-2);
  padding: 2rem;
  border-radius: 12px;
}
.contentwrapper:last-child {
  margin-bottom: 0 !important;
}
.emptyText {
  font-size: 1.6rem;
  font-weight: 500;
  color: var(--black-2);
  text-align: center;
  margin: 0 auto;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  background-color: var(--gray-2);
  width: max-content;
  height: auto;
}
